import { ImageUrl } from '../data/ImageUrl';
import { El } from '../services/ElementFactory';
export class Avatar {
    constructor() {
        this.element = El.makeDiv('avatar-container');
        this.image = El.makeImg(ImageUrl.PlayerTalk1, 'avatar-content');
        El.addElements(this.element, this.image);
    }
    get hidden() {
        return this.element.style.display === 'none';
    }
    set hidden(b) {
        if (b) {
            this.element.style.display = 'none';
        }
        else {
            this.element.style.removeProperty('display');
        }
    }
    setState(state) {
        switch (state) {
            case 'active':
                this.image.src = ImageUrl.PlayerTalk1;
                break;
            case 'passive':
                this.image.src = ImageUrl.PlayerWait1;
                break;
            case 'win':
                this.image.src = ImageUrl.PlayerWin1;
                break;
            case 'lose':
                this.image.src = ImageUrl.PlayerLose1;
                break;
            case 'neutral':
                this.image.src = ImageUrl.PlayerNeutral1;
                break;
            case 'ready':
                this.image.src = ImageUrl.PlayerReady1;
                break;
        }
    }
    faceRight(b) {
        if (b) {
            this.image.style.transform = 'scaleX(-1)';
        }
        else {
            this.image.style.removeProperty('transform');
            this.element.classList.add('avatar-right');
        }
    }
}
